<template>
	<view>
		<!-- 状态栏 -->
		<view v-if="showHeader" class="status" :style="{ position: headerPosition, top: statusTop, opacity: afterHeaderOpacity }"></view>
		<!-- 顶部导航栏 -->
		<view v-if="showHeader" class="header" :style="{ position: headerPosition, top: headerTop, opacity: afterHeaderOpacity }">
			<!-- 定位城市 -->
			<!-- <view class="addr">
				<view class="icon location"></view>
				{{ city }}
			</view> -->
			<!-- 扫一扫 -->
			<view class="addr">
				<view class="icon">
					<image src="/static/temp/scan.png"></image>
					<view class="text">扫一扫</view>
				</view>
			</view>
			<!-- 搜索框 -->
			<view class="input-box">
				<input placeholder="选你所选,爱你所爱" placeholder-style="color:#c0c0c0;" @tap="toSearch()" />
				<view class="icon search"></view>
			</view>
			<!-- 右侧图标按钮 -->
			<view class="icon-btn">
				<!-- 消息 -->
				<!-- <view class="iconClass">
					<image src="/static/temp/news.png"></image>
				</view> -->
				<view class="icon" @tap="toMsg">
					<image src="/static/temp/news.png"></image>
					<view class="text">消息</view>
				</view>
			</view>
		</view>
		<!-- 占位 -->
		<view v-if="showHeader" class="place"></view>
		<!-- 轮播图 -->
		<view class="swiper">
			<view class="swiper-box">
				<swiper circular="true" autoplay="true" @change="swiperChange">
					<swiper-item v-for="swiper in swiperList" :key="swiper.id"><image :src="swiper.img" @tap="toSwiper(swiper)"></image></swiper-item>
				</swiper>
				<view class="indicator"><view class="dots" v-for="(swiper, index) in swiperList" :class="[currentSwiper >= index ? 'on' : '']" :key="index"></view></view>
			</view>
		</view>
		<!-- 分类列表 -->
		<view class="category-list">
			<view class="category" v-for="(row, index) in categoryList" :key="index" @tap="toCategory(row)">
				<view class="img"><image :src="row.img"></image></view>
				<view class="text">{{ row.name }}</view>
			</view>
		</view>
		<!-- 功能区 -->
		<view class="function">
			<view class="list">
				<view
					class="cloumn"
					v-for="(row, index) in functionList"
					:key="index"
					:style="{ backgroundImage: 'url(' + row.img + ')', backgroundSize: '340rpx 140rpx' }"
					@tap="toCategory(row)"
				>
					<view class="left">
						<view class="title">{{ row.title }}</view>
						<view class="text">{{ row.text }}</view>
					</view>
					<view class="right"><image :src="row.img2"></image></view>
				</view>
			</view>
			<view class="list">
				<view
					class="cloumn"
					v-for="(row, index) in functionList2"
					:key="index"
					:style="{ backgroundImage: 'url(' + row.img + ')', backgroundSize: '340rpx 140rpx' }"
					@tap="toCategory(row)"
				>
					<view class="left">
						<view class="title">{{ row.title }}</view>
						<view class="text">{{ row.text }}</view>
					</view>
					<view class="right"><image :src="row.img2"></image></view>
				</view>
			</view>
		</view>
		<!-- 新闻滚动 -->
		<view class="news">
			<view class="content">
				<view class="title"><view class="text">资讯</view></view>
				<view class="textBox">
					<swiper class="swiper" vertical="true" autoplay="true" circular="true" interval="3000">
						<swiper-item v-for="(item, index) in msg" :key="index">
							<view class="text">{{ item }}</view>
						</swiper-item>
					</swiper>
				</view>
				<view class="more">更多+</view>
			</view>
		</view>
		<!-- 活动区 -->
		<view class="promotion">
			<!-- 卡片播放 -->
			<view>
				<!-- <Barousel
				    :list="barouselList"
					:width="690"
					:height="200"
				    >
				</Barousel> -->
				<swiper 
				circular="true" 
				autoplay="true" 
				@change="swiperChange"
				previous-margin="100rpx" 
				next-margin="100rpx"
				>
					<swiper-item v-for="swiper in barouselList" :key="swiper.id">
						<image 
						class="slide-image"
						:src="swiper.img" 
						@tap="toSwiper(swiper)"
						:class="[currentSwiper == index ? 'zoom-in' : 'zoom-out']"></image>
					</swiper-item>
				</swiper>
				<!-- <bw-img-upload style="width:100%" ></bw-img-upload>
				<bw-swiper 
				:swiperList="barouselList" 
				:w_h="3"
				:nextMargin="50"
				:previousMargin="50"
				style="width:100%">
				</bw-swiper> -->
			</view>
			<view class="list">
				<view class="column" v-for="(row, index) in Promotion" @tap="toPromotion(row)" :key="index">
					<view class="top">
						<view class="title">{{ row.title }}</view>
						<!-- <view class="countdown" v-if="row.countdown">
							<view>{{ row.countdown.h }}</view>
							:
							<view>{{ row.countdown.m }}</view>
							:
							<view>{{ row.countdown.s }}</view>
						</view> -->
					</view>
					<view class="left">
						<view class="ad">{{ row.ad }}</view>
						<!-- <view class="into">点击进入</view> -->
					</view>
					<view class="right"><image :src="row.img"></image></view>
				</view>
			</view>
		</view>
		<view class="paihang"><image src="/static/temp/xiaoliang.png"></image></view>
		<!-- 销量排行 -->
		<view class="goods-list">
			<view class="list">
				<view
					class="cloumn"
					v-for="(row, index) in sortList"
					:key="index"
					:style="{backgroundImage: 'url(' + row.img2 + ')', backgroundRepeat: 'no-repeat', backgroundSize: '100% 100%' }"
					@tap="toCategory(row)"
				>
					<view class="right"><image :src="row.img"></image></view>
					<view class="left">
						<view class="title">{{ row.title }}</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 推荐商品 -->
		<view class="tuijian"><image src="/static/temp/tuijian.png"></image></view>
		<view class="like">
			<view class="product-list">
				<view class="product" v-for="product in productList" :key="product.goods_id" @tap="toGoods(product)">
					<image mode="widthFix" :src="product.img"></image>
					<view class="name">{{ product.name }}</view>
					<view class="info">
						<view class="price">{{ product.price }}</view>
						<view class="slogan">{{ product.slogan }}</view>
					</view>
				</view>
			</view>
			<view class="loading-text">{{ loadingText }}</view>
		</view>
	</view>
</template>

<script>
var ttt = 0;
//高德SDK
import amap from '@/common/SDK/amap-wx.js';
// 卡片化轮播
import Barousel from '@/components/barousel/barousel.vue';
import bwSwiper from '@/wxcomponents/bw-swiper/bw-swiper.vue'

export default {
	components: {
		Barousel,
		bwSwiper
	},
	data() {
		return {
			showHeader: true,
			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			nVueTitle: null,
			city: '北京',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [
				{ id: 1, src: 'url1', img: '/static/temp/banner.png' },
				{ id: 2, src: 'url2', img: '/static/temp/banner.png' },
				{ id: 3, src: 'url3', img: '/static/temp/banner.png' }
			],
			// 分类菜单
			categoryList: [
				{ id: 1, name: '活动中心', img: '/static/temp/activity_center.png' },
				{ id: 2, name: '品牌中心', img: '/static/temp/brand.png' },
				{ id: 3, name: '签到中心', img: '/static/temp/sign_In.png' },
				{ id: 4, name: '积分商城', img: '/static/temp/integral.png' },
				{ id: 5, name: '领劵中心', img: '/static/temp/securities.png' }
			],
			// 功能菜单
			functionList: [
				{ id: 1, title: '商家入驻', text: '打造一站式服务商家为您带来更多利益', img: '/static/temp/business.png', img2: '/static/temp/ruzhu.png' },
				{ id: 2, title: '一键开店', text: '嫌麻烦？想要更简单？量贩库助您一键开店', img: '/static/temp/shop.png', img2: '/static/temp/woyaokaidian.png' }
				// { id: 3, title: '试用申请', text:'不知道好不好？免费试用您知道', img: '/static/temp/try.png',img2:'/static/temp/wodeshiyong.png' },
				// { id: 4, title: '牛人分享', text:'不会没关系！！大神，牛人各种教', img: '/static/temp/share.png',img2:'/static/temp/shipin.png' }
			],
			functionList2: [
				{ id: 3, title: '试用申请', text: '不知道好不好？免费试用您知道', img: '/static/temp/try.png', img2: '/static/temp/wodeshiyong.png' },
				{ id: 4, title: '牛人分享', text: '不会没关系！！大神，牛人各种教', img: '/static/temp/share.png', img2: '/static/temp/shipin.png' }
			],
			// 活动轮播
			barouselList: [
				{ id: 1, img: '/static/temp/share.png' }, 
				{ id: 2, img: '/static/temp/shop.png' }, 
				{ id: 3, img: '/static/temp/business.png' }
			],
			Promotion: [],
			//猜你喜欢列表
			productList: [
				{
					goods_id: 0,
					img: '/static/temp/kouhong.png',
					name: '正品Dior/迪奥口红烈焰蓝金756# CD唇膏夹心滋润哑光',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 1,
					img: '/static/temp/ping.png',
					name: '正品Dior/迪奥口红烈焰蓝金756# CD唇膏夹心滋润哑光',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 2,
					img: '/static/temp/kouhong.png',
					name: '正品Dior/迪奥口红烈焰蓝金756# CD唇膏夹心滋润哑光',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 3,
					img: '/static/temp/ping.png',
					name: '正品Dior/迪奥口红烈焰蓝金756# CD唇膏夹心滋润哑光',
					price: '￥168',
					slogan: '1235人付款'
				}
			],
			loadingText: '正在加载...',
			// 新闻列表
			msg: ['这个群体习近平一直惦念于心 老师喊你回学校上课', '中国稳健前行|勇于自我革命:中国共产党的鲜明品格', '中国稳健前行|勇于自我革命:中国共产党的鲜明品格'],
			// 销量排行列表
			sortList: [
				{ id: 1, title: '正品Dior/迪奥口红烈焰蓝金 756#CD唇膏夹心滋润哑光', img: '/static/temp/kouhong.png', img2: '/static/temp/1.png' },
				{ id: 2, title: '正品Dior/迪奥口红烈焰蓝金 756#CD唇膏夹心滋润哑光', img: '/static/temp/ping.png', img2: '/static/temp/2.png' },
				{ id: 3, title: '正品Dior/迪奥口红烈焰蓝金 756#CD唇膏夹心滋润哑光', img: '/static/temp/kouhong.png', img2: '/static/temp/3.png' }
			]
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
		let len = this.productList.length;
		if (len >= 40) {
			this.loadingText = '到底了';
			return false;
		}
		// 演示,随机加入商品,生成环境请替换为ajax请求
		let end_goods_id = this.productList[len - 1].goods_id;
		for (let i = 1; i <= 10; i++) {
			let goods_id = end_goods_id + i;
			let p = {
				goods_id: goods_id,
				img: '/static/img/goods/p' + (goods_id % 10 == 0 ? 10 : goods_id % 10) + '.jpg',
				name: '商品名称商品名称商品名称商品名称商品名称',
				price: '￥168',
				slogan: '1235人付款'
			};
			this.productList.push(p);
		}
	},
	onLoad() {
		// #ifdef APP-PLUS
		this.nVueTitle = uni.getSubNVueById('homeTitleNvue');
		this.nVueTitle.onMessage(res => {
			let type = res.data.type;
			if (type == 'focus') {
				this.toSearch();
			}
		});
		this.showHeader = false;
		this.statusHeight = plus.navigator.getStatusbarHeight();
		// #endif
		this.amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '7c235a9ac4e25e482614c6b8eac6fd8e'
		});
		//定位地址
		this.amapPlugin.getRegeo({
			success: data => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
				// #ifdef APP-PLUS
				this.nVueTitle.postMessage({ type: 'location', city: this.city });
				// #endif
			}
		});
		//开启定时器
		this.Timer();
		//加载活动专区
		this.loadPromotion();
		console.log(this, 'home');
	},
	computed: {},
	mounted() {},
	methods: {
		//加载Promotion 并设定倒计时,,实际应用中应该是ajax加载此数据。
		loadPromotion() {
			let cutTime = new Date();
			let yy = cutTime.getFullYear(),
				mm = cutTime.getMonth() + 1,
				dd = cutTime.getDate();
			let tmpcountdown = yy + '/' + mm + '/' + dd + ' 23:59:59';
			let tmpPromotion = [
				{
					title: '预售新品',
					ad: '天天新品专区',
					img: '/static/temp/kouhong.png',
					countdown: false
				},
				{
					title: '限时抢购',
					ad: '每天23点上线',
					img: '/static/temp/ping.png',
					countdown: tmpcountdown
				} //countdown为目标时间，程序会获取当前时间倒数
			];
			//检查倒计时
			for (let i = 0; i < tmpPromotion.length; i++) {
				let row = tmpPromotion[i];
				if (row.countdown) {
					let h = '00',
						m = '00',
						s = '00';
					let currentTime = new Date();
					let cutoffTime = new Date(tmpcountdown);
					if (!(currentTime >= cutoffTime)) {
						let countTime = parseInt((cutoffTime.getTime() - currentTime.getTime()) / 1000);
						h = parseInt(countTime / 3600);
						m = parseInt((countTime % 3600) / 60);
						s = countTime % 60;
						h = h < 10 ? '0' + h : h;
						m = m < 10 ? '0' + m : m;
						s = s < 10 ? '0' + s : s;
					}
					tmpPromotion[i].countdown = { h: h, m: m, s: s };
				}
			}
			this.Promotion = tmpPromotion;
		},
		//定时器
		Timer() {
			setInterval(() => {
				if (this.Promotion.length > 0) {
					for (let i = 0; i < this.Promotion.length; i++) {
						let row = this.Promotion[i];
						if (row.countdown) {
							if (!(row.countdown.h == 0 && row.countdown.m == 0 && row.countdown.s == 0)) {
								if (row.countdown.s > 0) {
									row.countdown.s--;
									row.countdown.s = row.countdown.s < 10 ? '0' + row.countdown.s : row.countdown.s;
								} else if (row.countdown.m > 0) {
									row.countdown.m--;
									row.countdown.m = row.countdown.m < 10 ? '0' + row.countdown.m : row.countdown.m;
									row.countdown.s = 59;
								} else if (row.countdown.h > 0) {
									row.countdown.h--;
									row.countdown.h = row.countdown.h < 10 ? '0' + row.countdown.h : row.countdown.h;
									row.countdown.m = 59;
									row.countdown.s = 59;
								}
								this.Promotion[i].countdown = row.countdown;
							}
						}
					}
				}
			}, 1000);
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../../msg/msg'
			});
		},
		//搜索跳转
		toSearch() {
			uni.showToast({ title: '建议跳转到新页面做搜索功能' });
		},
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({ title: e.src, icon: 'none' });
		},
		//分类跳转
		toCategory(e) {
			console.log(e)
			uni.setStorageSync('catName', e.name);
			uni.navigateTo({
				url: '../../activity/assemble/assemble'
			});
		},
		// /?cid=' + e.id + '&name=' + e.name
		//推荐商品跳转
		toPromotion(e) {
			uni.showToast({ title: e.title, icon: 'none' });
		},
		//商品跳转
		toGoods(e) {
			uni.showToast({ title: '商品' + e.goods_id, icon: 'none' });
			uni.navigateTo({
				url: '../../goods/goods'
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		
	}
};
</script>
<style lang="scss">
page {
	position: relative;
	background-color: #fff;
}

@font-face {
	font-family: 'HMfont-home';
	src: url('data:application/x-font-woff2;charset=utf-8;base64,')
		format('woff2');
}

.pullDown-effects {
	position: fixed;
	//top: calc(100upx - 36vw);
	top: 0;
	z-index: 9;
	width: 100%;
	height: 36vw;
	/*  #ifdef  APP-PLUS  */
	padding-top: var(--status-bar-height);
	/*  #endif  */
	image {
		width: 100%;
		height: 36vw;
	}
}
.status {
	width: 100%;
	height: 0;
	position: fixed;
	z-index: 10;
	background-color: #fff;
	top: 0;
	/*  #ifdef  APP-PLUS  */
	height: var(--status-bar-height); //覆盖样式
	/*  #endif  */
}
.header {
	width: 92%;
	padding: 0 4%;
	height: 100upx;
	display: flex;
	align-items: center;
	position: fixed;
	top: 0;
	z-index: 10;
	background-color: #fff;
	/*  #ifdef  APP-PLUS  */
	top: var(--status-bar-height);
	/*  #endif  */

	.addr {
		width: 60rpx;
		height: 60rpx;
		flex-shrink: 0;
		display: flex;
		align-items: center;
		.icon {
			width: 60rpx;
			height: 60rpx;
			display: flex;
			flex-wrap: wrap;
			image {
				width: 35rpx;
				height: 35rpx;
				padding-left: 6rpx;
			}
			.text {
				width: 60rpx;
				font-size: 18rpx;
				font-weight: 500;
				z-index: 10;
			}
		}
	}
	.input-box {
		width: 100%;
		height: 60upx;
		// background-color: #f5f5f5;
		border-radius: 30upx;
		position: relative;
		display: flex;
		align-items: center;
		border: 1px solid #303133;
		.icon {
			display: flex;
			align-items: center;
			position: absolute;
			top: 0;
			// right: 0;
			left: 20upx;
			width: 60upx;
			height: 60upx;
			font-size: 34upx;
			color: #c0c0c0;
		}
		input {
			padding-left: 60upx;
			height: 28upx;
			font-size: 28upx;
		}
	}
	.icon-btn {
		width: 60rpx;
		height: 60rpx;
		flex-shrink: 0;
		display: flex;
		padding-left: 10rpx;
		.icon {
			width: 60rpx;
			height: 60rpx;
			display: flex;
			flex-wrap: wrap;
			image {
				width: 35rpx;
				height: 35rpx;
				padding-left: 8rpx;
			}
			.text {
				width: 60rpx;
				font-size: 18rpx;
